.exclusivemenu-container{
    background-color: #161616;
   padding: 30px calc(50% - 530px);
   .title{
      margin-bottom: 55px;
      .number{
        color: rgb(255, 255, 255);
        font-weight: 500;
    letter-spacing: 1px;
    margin-bottom: 10px;
    text-align: center;
      }
    
    .wrapper{
      align-items: center;
    display: flex;
    justify-content: center;
    width: 100%;
    .titlea{
      font-size: 32px;
    font-weight: 800;
    letter-spacing: 1.6px;
    line-height: 1;
    padding: 0 55px;
    text-align: center;
    color: rgb(255, 255, 255);
  
    }
    .decoration{
      
      border: 1px solid #161616;
      border-color: rgb(255, 255, 255);
    display: flex;
    flex: 1;
    height: 2px;
    }
    }
    .subtitle{
      font-weight: 300;
    letter-spacing: 1.5px;
    margin-top: 12px;
    text-align: center;
    color: rgb(255, 255, 255);
    }
  }
  
  .exclusivemenu-menu-list{
    display: flex;
    flex-wrap: wrap;
      
    .menu-item{
      margin-right: 33px;
          min-height: 230px;
          flex-basis: calc(33% - 33px);
          width: 200px;
          .menu-img{
            margin-bottom: 20px;
            padding-top: 100%;
            
            
            position: relative;
            .imga{
              background-image: url(https://ttfb-bs-s3.25sprout.work/upload/menu/image/20210526-cb9654deffb16ac758a39ecf241f1e74.jpg);
              align-items: center;
                  background-position: 50%;
                  background-size: cover;
                  display: flex;
                  height: 100%;
                  justify-content: flex-end;
                  left: 0;
                  position: absolute;
                  top: 0;
                  width: 100%;
                  
            }
          
          
          }
      
      .menu-container{
        margin: 0 20px;
        .menu-header{
          align-items: center;
            display: flex;
            margin-bottom: 8px;
            color: rgb(255, 255, 255);
            font-size: 25px;
            font-weight: 700;
    color: rgb(255, 255, 255);
        }
        .menu-price{
   color: rgb(255, 255, 255);
   font-size: 22px;
            font-weight: 700;
            letter-spacing: 1.1px;
            margin-bottom: 8px;
            color: rgb(255, 255, 255);
            font-size: 25px;
            font-weight: 700;
        }
        .menu-info{
          color: rgb(255, 255, 255);
          letter-spacing: .5px;
          line-height: 1.57;
          margin-bottom: 8px;
          text-align: justify;
        }
  
      }
    }
  }
   
  }